#github {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding-top: 70px;
}

#github-calendar{
    display: flex;
    justify-content: center;
    width: 80vw;
}

#stats-container {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2vw;
    margin:2vh 0;
}

@media only screen and (max-width: 609px) {
    #stats-container {
        display: flex;
        flex-direction: column;
    }

    #github-stats-card {
        height: 20vh;
        width: 80vw;
    }
    #github-streak-stats{
        height: 20vh;
        width: 65vw;
    }

}