.navbar {
  display: flex;
  align-items: center;
  width: 100%;
  top: 0;
  position: fixed;
  z-index:20;
  justify-content: space-between;
  background-color: rgb(241, 237, 233);
  margin-bottom: 2vh;
}

.nav-logo {
  margin-left: 5vw;
}

nav{
  display: flex;
  width: 60%;
  justify-content: space-evenly;
  margin-right: 1vw;
}

nav a {
  text-decoration: none;
  color: black;
  margin: 10px 1vw;
  font-size: 20px;
}

.nav-close a {
  text-decoration: none;
  color: black;
  margin: 10px 0;
  font-size: 20px;
}

.changer {
  display: none;
}

.menu-button {
  display: block;
  padding: 10px;
  margin-right: 20px;
  visibility: hidden;
}

.menu {
  font-size: 30px;
}

.nav-open {
  display: flex;
}

nav .about:hover {
  background-color: #ffa500;
  color: white;
}

nav .skills:hover {
  background-color: #800080;
  color: white;
}

nav .instructions:hover {
  background-color: #800080;
  color: white;
}

nav .home:hover {
  background-color: #008000;
  color: white;
}

nav .contact:hover {
  background-color: #00bfff;
  color: white;
}

nav .projects:hover {
  background-color: #f44336;
  color: white;
}

nav .contact:hover {
  background-color: #00bfff;
  color: white;
}

nav .resume:hover{
  background-color: black;
  color: white;
}
nav .nav-link_about_current{
  background-color: #ffa500;
  color: white;
}

nav .nav-link_instructions_current{
  background-color: #800080;
  color: white;
}

nav .nav-link_home_current{
  background-color: #008000;
  color: white;
}

nav .nav-link_contact_current{
  background-color: #00bfff;
  color: white;
}

nav .nav-link_skills_current{
  background-color: #800080;
  color: white;
}

nav .nav-link_projects_current{
  background-color: #f44336;
  color: white;
}

@media (max-width: 768px) {
  .nav-logo {
    margin-left: 3vw;
  }

  .nav-close{
    display: none;
  }
  
  nav {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 40px;
  }

  nav>a {
    margin: 10px 0;
    font-size: 24px;
  }

  .changer {
    display: none;
  }

  .menu-button {
    visibility: visible;
  }

  .menu {
    font-size: 36px;
  }
}