#home {
     display: flex;
     align-items: center;
     justify-content: center;
     max-width: 800px;
     margin: 0 auto;
     padding: 80px;
     gap: 5vw;
}

#home_content {
     
}

.home_heading {
     font-size: 50px;
}

#home_content>.about_intro{
     @media only screen and (max-width: 480px) {
          font-size:20px
     }
}
.home-img {
     width: 80%;
     max-width: 400px;
     margin: 0 auto;
}

.resume_download {
     display: flex;
     margin-top: 2em;
     text-align: center;
}

.resume_btn {
     width: 8vw;
     height: 6vh;
     background-color: rgb(188, 183, 183);
     cursor: pointer;
}

.resume_btn span {
     margin-right: 0.5em;
}

@media screen and (max-width: 767px) {
     #home__content {
          padding: 0 1rem;
     }

     .home_heading {
          font-size: 36px;
          margin-bottom: 1.5rem;
     }

     .home-img {
          width: 100%;
          max-width: none;
          margin-bottom: 1.5rem;
     }

     .resume_btn {
          width: 100%;
          font-size: 14px;
          padding: 0.5em 1em;
     }
}